import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: () => import('@/views/home.vue')
  },
  {
    path: '/aoding',
    name: 'aoding',
    component: () => import('@/views/aoding/index.vue')
  },
  {
    path: '/aodingScript',
    name: 'aodingScript',
    component: () => import('@/views/aoding/others/aodingScript.vue')
  },
  {
    path: '/configuration',
    name: 'configuration',
    component: () => import('@/views/aoding/others/configuration.vue')
  },
  {
    path: '/setting',
    name: 'webrtc',
    component: () => import('@/views/setting.vue')
  },
  {
    path: '/result',
    name: 'result',
    component: () => import('@/views/aoding/others/result.vue')
  },
  {
    path: '/webrtc',
    name: 'webrtc',
    component: () => import('@/views/webrtc.vue')
  },
  {
    path: '/cropping',
    name: 'cropping',
    component: () => import('@/views/cropping.vue')
  },
  {
    path: '/mark',
    name: 'mark',
    component: () => import('@/views/mark.vue')
  }
]

const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const router = new VueRouter({
  routes
})

export default router
