import Vue from 'vue'
import App from './App.vue'
import router from './router'
import wallpaper1 from '@/assets/bag/bg1.jpg';
import wallpaper2 from '@/assets/bag/bg2.jpg';
import wallpaper3 from '@/assets/bag/bg3.jpg';
import wallpaper4 from '@/assets/bag/bg4.jpg';
import store from './store'
import '@/assets/style/index.css'
import Peer from 'peerjs'


Vue.prototype.$globalConfig = {
  currentWallpaper: getbag(), // 壁纸
  secretPassword: localStorage.getItem('secretPassword')?localStorage.getItem('secretPassword'): '', // 授权码
  v2: localStorage.getItem('v2') || true
}

function getbag() {
  var bag = wallpaper1,Wallpapers=localStorage.getItem('Wallpapers')
  if(Wallpapers) {
    if(Wallpapers == 0) {
      bag = wallpaper1
    } else if(Wallpapers == 1) {
      bag = wallpaper2
    } else if(Wallpapers == 2) {
      bag = wallpaper3
    } else if(Wallpapers == 3) {
      bag = wallpaper4
    } else {
      bag = Wallpapers
    }
  }
  return bag
}

// 定义全局方法
Vue.prototype.$Peer = Peer // 将PeerJS挂载到Vue原型上

// 返回上一页
Vue.prototype.$goBackOrHome = function() {
  if (window.history.length > 1) {
    // 有历史记录，返回上一页
    this.$router.go(-1)
  } else {
    // 没有历史记录，返回首页
    this.$router.push('/')
  }
}

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
